import kleur from "kleur"

const prefixes = {
    main: kleur.yellow().bold('[MVCraft]'),
    app: kleur.red().bold('[App]'),
    app: kleur.red().bold('[Module]'),
    cors: kleur.red().bold('[CORS]'),
    controller: kleur.green().bold('[Controller]'),
    service: kleur.green().bold('[Service]')
}

const Logger = (msg, prefix) => {

    let output = prefixes.main
    if(prefix && prefixes[prefix]) output += ' ' + prefixes[prefix]
    output += ' ' + msg

    console.log(output)

}

export default Logger